const settings = {
  slidesToShow: 1,
  slidesToScroll: 1,
  dots: true,
  infinite: true,
  speed: 1000,
  fade: true,
  autoplay: true,
  autoplaySpeed: 2000,
  nextArrow: <SliderNext />,
  prevArrow: <SliderPrev />,
};

const Carousel = () => {
  return (
    <div id="features">
      <div className="features__container">
        <img className="features__monitor" src={sliderBg} alt="" />
        <Slider className="features__slider" {...settings}>
          <div className="item">
            <div className="slider__descr">
              <h2>Earn Massive Revenue</h2>
              <p>
                Join the most rewarding affiliate program today and turn your
                traffic into cash.
              </p>
              <ul className="mainList">
                <li>Earn Up To 50% Revenue Share</li>
                <li>Popular Casino Brands</li>
                <li>High Conversion Rates</li>
              </ul>
            </div>
            <div className="slider__image">
              <img className="lazy" src={slide1} alt="Featured image 1" />
            </div>
          </div>

          <div className="item">
            <div className="slider__descr">
              <h2>Promote Today</h2>
              <p>
                Who wants to wait for lengthy approval processes? Sign up in
                seconds and start earning now.
              </p>
              <ul className="mainList">
                <li>Fast Payouts</li>
                <li>High end Industry Driven Brands</li>
                <li>No Fuss</li>
              </ul>
            </div>
            <div className="slider__image">
              <img className="lazy" src={slide2} alt="Featured image 2" />
            </div>
          </div>

          <div className="item">
            <div className="slider__descr">
              <h2>Powerful Tools</h2>
              <p>
                Visualise your earnings in a brand new way with our custom built
                affiliate platform. Designed for your convenience.
              </p>
              <ul className="mainList">
                <li>High Converting Creatives</li>
                <li>Cutting Edge Reporting</li>
                <li>Instant Analytics Across Multiple Cost Metrics</li>
              </ul>
            </div>
            <div className="slider__image">
              <img className="lazy" src={slide3} alt="Featured image 3" />
            </div>
          </div>
        </Slider>
      </div>
    </div>
  );
};

export default Carousel;
